@import "../../ui/fonts";
@import "../../ui/colors";
@import "../../ui/values";

@mixin keyframes($animation-name) {
  @-webkit-keyframes #{$animation-name} {
    @content;
  }
  @-moz-keyframes #{$animation-name} {
    @content;
  }
  @-ms-keyframes #{$animation-name} {
    @content;
  }
  @-o-keyframes #{$animation-name} {
    @content;
  }
  @keyframes #{$animation-name} {
    @content;
  }
}

@mixin animation($str) {
  -webkit-animation: #{$str};
  -moz-animation: #{$str};
  -ms-animation: #{$str};
  -o-animation: #{$str};
  animation: #{$str};
}

@include keyframes(rotate-icon1) {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@include keyframes(rotate-icon2) {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.home {
  min-height: 100vh;
  padding: calc(#{$navbarheight} + 2rem) 2rem 2rem 2rem;
  background: radial-gradient(
    ellipse at bottom,
    #1b2735 0%,
    #090a0f 50%,
    #090a0f 100%
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;

  .image {
    width: 25rem;
    height: 25rem;
    border-radius: 50%;
  }

  .imagePlaceholder {
    width: 25rem;
    height: 25rem;
    border-radius: 50%;
    position: absolute;
  }

  .title {
    margin: 1rem 0 1rem 0;
    font-size: 2.4rem;
    font-weight: 500;
    text-align: center;
    color: $quaternary;
  }

  .about {
    font-size: 1.5rem;
    font-weight: 400;
    color: $quaternary;
    text-align: center;
  }

  .about_next {
    margin-bottom: 1.5rem;
    font-size: 1rem;
    font-weight: 400;
    color: #eeeeee80;
    text-align: center;
  }

  .links {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    a {
      padding: 10px;
      text-decoration: none;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 0 8px rgba(238, 238, 238, 0.5);
      font-size: 1.8rem;

      & svg {
        transition: all 1.2s ease;
      }

      &:hover svg {
        transform: rotateY(1turn);
      }

      /*
      &:hover:nth-child(odd) {
        @include animation("rotate-icon1 1s 1");
      }

      &:hover:nth-child(even) {
        @include animation("rotate-icon2 1s 1");
      }
      */
    }
  }
}

@media (max-width: 500px) {
  .home {
    .image {
      height: 18rem;
      width: 18rem;
    }
    .imagePlaceholder {
      height: 18rem;
      width: 18rem;
      position: absolute;
    }

    .title {
      font-size: 1.5rem;
    }

    .about {
      font-size: 0.8rem;
    }
    .about_next {
      font-size: 0.8rem;
    }
  }
}
