@import "./ui/fonts";
@import "./ui/colors";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Ubuntu", sans-serif;
  overflow-x: hidden;
  background-color: $primary;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: rgba(0, 0, 0, 0.5);
}
::-webkit-scrollbar-thumb {
  background-color: #000000;
  border-radius: 4px;
}
