@import "../../ui/colors";

.projectCard {
  width: 300px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(128, 128, 128, 0.8);
  overflow: hidden;

  .innershadow {
    position: absolute;
    bottom: 0;
    height: 50%;
    width: 100%;
  }

  .imagewrapper {
    width: 100%;
    height: 200px;
    background-color: $quaternary;
    border-bottom: 1.5px solid #808080cc;

    img {
      height: 100%;
      width: 100%;
      object-fit: contain;
      position: relative; //
      background-image: linear-gradient(
        to top,
        #000000 0%,
        #000000 10%,
        transparent 50%
      );
    }
    video {
      height: 100%;
      width: 100%;
      object-fit: contain;
      position: relative; // Added this line to make the image/video tag a positioning context
    }

    img::after,
    video::after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 50%; // height of the gradient
      background: linear-gradient(
        transparent,
        rgba(0, 0, 0, 0.8)
      ); // the gradient itself
    }
  }

  .content {
    padding: 15px;
    background-color: $quaternary;
    color: $primary;

    h2 {
      font-weight: 500;
      font-size: 1.4rem;
      margin-bottom: 0.4rem;
      white-space: nowrap;
      overflow: hidden;
      display: block;
      text-overflow: ellipsis;
    }

    p {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      line-height: 1.6rem;
    }
  }

  .buttons {
    margin: 20px 0 10px 0;
    display: flex; // Use Flexbox layout

    .github,
    .hosted {
      text-decoration: none;
      font-size: 1rem;
      color: $primary;
      padding: 10px 20px; // Add padding for spacing around the buttons
      transition: color 0.3s; // Adding a smooth transition effect for the text color change
    }
    // Use Flexbox to distribute space around buttons evenly
    justify-content: space-around;

    // Hover styles
    .github:hover,
    .hosted:hover {
      color: $tertiary; // Change the text color on hover
    }
  }
}
