@import "../../ui/colors";
@import "../../ui/values";

.blog {
  min-height: 100vh;
  padding: calc(#{$navbarheight} + 1rem) 0 2rem 0;
  background: radial-gradient(
    ellipse at bottom,
    #1b2735 0%,
    #1b2735 20%,
    #090a0f 100%
  );

  .wrapper {
    max-width: 1080px;
    margin: 20px auto;
    padding: 0 20px;
    position: relative;
    margin-top: 1rem;

    .blog_content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      gap: 5rem;

      a {
        font-size: 3.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        border-radius: 10px;
        box-shadow: 0 0 6px rgba(0, 0, 0, 0.4);
        transition: 0.5s all ease-in-out !important;

        &:hover {
          border-radius: 50px;
          background: $tertiary;
          box-shadow: 0 0 6px rgba(111, 125, 252, 0.6);
        }

        & svg {
          transition: all 0.3s ease-in-out;
        }

        &:hover svg {
          color: $quaternary !important;
        }
      }
    }
  }
}

.labelContainer {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

.label {
  background-color: $tertiary;
  color: white;
  padding: 8px;
  border-radius: 4px;
  font-size: x-small;
}

.link {
  font-size: x-small;
  color: white;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

@media (max-width: 500px) {
  .blog {
    .wrapper {
      .blog_content {
        gap: 2rem;

        a {
          font-size: 2.5rem;
        }
      }
    }
    .labelContainer {
      display: flex;
      justify-content: center;
      margin-bottom: 10px;
    }
  }
}
