@import "../../ui/colors";
@import "../../ui/values";

.projects {
  min-height: 100vh;
  padding: calc(#{$navbarheight} + 1rem) 0 2rem 0;
  padding-top: $navbarheight;
  background: radial-gradient(
    ellipse at bottom,
    #1b2735 0%,
    #1b2735 20%,
    #090a0f 100%
  );
  overflow-x: hidden;

  .wrapper {
    max-width: 1080px;
    margin: 20px auto;
    padding: 0 20px;
    position: relative;
    margin-top: 2rem;
    padding-bottom: 2rem;

    .projects_content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      gap: 3.6rem;
    }
  }
}
